*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

.candidateimage{

    height: 250px;
    border-radius: 10px;
    border: 1px solid black;
}

button{
    background-color: orangered;
    margin-top: 20px;
}

.introparent{
    /* border: 1px solid red; */
    background-color:rgba(0, 255, 255, 0.583);
    padding-top: 20px;
    padding-bottom: 50px;
}

.introname{

    /* border: 1px solid green; */
    display: flex;
    width: 70%;
    margin: auto;
    text-align: left;
    padding: 30px;

}
.introname > div{
    /* border:1px solid blue */
}

.candidatename{
    font-size: 60px;
}

.aboutme > p:nth-child(1) {
    font-size: 50px;
    font-weight: 300;
}

.aboutme > p:nth-child(2){
    width: 80%;
    margin: auto;
    text-align: left;
}


@media screen and (max-width:756px) {
    

    .candidateimage{
        height:200px ;
    }

    .introname{
        width: 85%;
        padding: 0px;
    }
    .candidatename{
        font-size: 40px;
    }
}

@media screen and (max-width:320px) {
    

    .candidateimage{
        height:150px ;
    }

    .introname{
        width: 85%;
        padding: 0px;
    }
    .candidatename{
        font-size: 30px;
    }
}