*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.projectparent{
    background-color: rgba(255,0,0,.289);
    padding-bottom:40px
}

.headingproject{
    font-size: 60px;
    font-weight: 100;
    height: 120px;
    background-color: rgba(255,0,0,.289)
}

.image3{
    width: 100%;
}

.pcard{
    border: 1px solid black;
    border-radius: 4px;
    width: 350px;
    height: 480px;
    margin: auto;
    background-color: #FF8C00;
}

.proinfo{
    padding: 10px;
}

.projectparent{
    display: flex;
}

@media screen and (max-width:1064px) {
    
    .projectparent{
        display: flex;
        flex-direction: column;
    }

    .pcard{
        margin-bottom: 30px;
    }
    
}

@media screen and (max-width:354px) {

    .pcard{
        width: 300px;
        height: auto;
    }


}